 */

 .wp-block-crosswinds-blocks-accordions {


	.wp-block-crosswinds-blocks-accordion {

		.wp-block-crosswinds-blocks-accordion-title {
			position: relative;

			.accordion-icon::before {
				position: absolute;
				right: 2em;
				top: 20%;
				font-size: 20px;
				font-family: 'Font Awesome 6 Pro';
				content: "\2b";
			}

			.accordion-icon.arrows::before {
				content: "\f107";
			}

			&[aria-expanded="true"] {
				.accordion-icon::before {
					content: "\f068";
				}

				.accordion-icon.arrows::before {
					content: "\f106";
				}
			}
		}

		.wp-block-crosswinds-blocks-accordion-body {
			transition: max-height 0.15s ease-out;
			max-height: 1500px;

		}
	}
}
